body {
    background-color: rgb(33, 40, 53);
    
}

.App {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 200px;
}

.btn {
    width: 50px;
    height: 50px;
    border: none;
    border-radius: 15px;
    font-size: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: rgb(10, 44, 92);

}

.btn:hover {
    background-color: rgb(8, 25, 49);
}
